
.info-text {
  text-align: center;
  font-family: 'Dongle';
  color: var(--upperColor);
  text-shadow: -1px -1px 0 var(--botColor), 1px -1px 0 var(--botColor), -1px 1px 0 var(--botColor), 1px 1px 0 var(--botColor);
}

.choice {
  padding-left: 10px;
  padding-right: 10px;
  transition: transform .2s;
}

.choiceButton {
  
  border-style: solid;
  border-width: 15px 15px;
  border-radius: 50px;
}

.choiceButton:hover {
  transform: scale(1.2);
}

.choiceButton.active {
  background-color: var(--upperColor);
}

.circle {
  transition: transform .2s;
  user-select: none;
}

.circle:hover {
  filter: brightness(120%);
  transform: scale(1.2);
}

.circle.active {
  background-color: var(--upperColor);
  color: white;
}
